:root {
    scroll-behavior: inherit;
    --primary: #e23e9d;
    --primary-rgb: 226, 62, 157;
    --white: #ffffff;
    --black: #000000;
    --light-gray: #efefef;
    --light-blue: #e7eaf2;
    --footer-bg: #1f1f1f;
    --footer-text: #8d8d8d;
    --border-color: #e0e0e0;
    --yellow: #ffa913;
    --dark: #313233;
    --body-color: #585858;
    --error: #fe2724;
    --transition: all 0.3s ease-in-out;
}
.header-wrap{
    padding: 1rem 0 0.5rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.logo{
    margin-left: 180px;
    
}

.right-login{
    margin-right: 180px;
    display: flex;
    justify-content: space-between;
}
.category-form form{
    display: flex;
    align-items: center;
    flex-shrink: 0;
    background-color: var(--white);
    min-width: 600px;
    margin: 0 1.5rem;
    box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.33);      
}
.category-form form input[type=text] {
    background: none;
    border: none;
    padding: 0.7rem 0rem 0.7rem 0.7rem;
    width: 100%;
}
.category-form form input[type=text]:focus{
    outline: none;
    box-shadow: none;
    border: none;
}
.category-form form .custom-select{
    border: none;
}
.category-form form button{
    background-color: var(--black);
    border: none;
    color: var(--white);
    margin-left: 0.7rem;
    padding: 0.7rem 1rem;
}
.right-login .shop-btns{
    margin-left: 5px;
}
.right-login .shop-btns a{
    font-size: 1rem;
    color: var(--black);
}
.right-login .shop-btns a:hover{
    color: var(--primary);
}
.right-login .shop-btns a:not(:last-child){
    margin-right: 1rem;
}
.bs-links {
    display: flex;
    margin: 0;
    margin-right: 5px;
}
.bs-links a{
    color: var(--black);
    font-size: 1rem;
    font-weight: 500;
}

.bs-links a:hover{
    color: var(--primary);
}
.loginSignup:hover{
    color: var(--primary);
}