.navbar-toggler {
    border: none;
    padding: 0;
    outline: none;
}
.navbar-toggler:focus {
    box-shadow: none;
}
.navbar-toggler .hamburger-toggle {
    position: relative;
    display: inline-block;
    width: 50px;
    height: 50px;
    z-index: 1;
    
}
.navbar-toggler .hamburger-toggle .hamburger {
    position: absolute;
    transform: translate(-50%, -50%) rotate(0deg);
    left: 50%;
    top: 50%;
    width: 50%;
    height: 50%;
    pointer-events: none;
}
.navbar-toggler .hamburger-toggle .hamburger span {
    width: 100%;
    height: 2px;
    position: absolute;
    background: #333;
    border-radius: 1px;
    z-index: 1;
    transition: transform 0.2s cubic-bezier(0.77, 0.2, 0.05, 1), background 0.2s cubic-bezier(0.77, 0.2, 0.05, 1), all 0.2s ease-in-out;
    left: 0px;
}
.navbar-toggler .hamburger-toggle .hamburger span:first-child {
    top: 10%;
    transform-origin: 50% 50%;
    transform: translate(0% -50%) !important;
}
.navbar-toggler .hamburger-toggle .hamburger span:nth-child(2) {
    top: 50%;
    transform: translate(0, -50%);
}
.navbar-toggler .hamburger-toggle .hamburger span:last-child {
    left: 0px;
    top: auto;
    bottom: 10%;
    transform-origin: 50% 50%;
}
.navbar-toggler .hamburger-toggle .hamburger.active span {
    position: absolute;
    margin: 0;
}
.navbar-toggler .hamburger-toggle .hamburger.active span:first-child {
    top: 45%;
    transform: rotate(45deg);
}
.navbar-toggler .hamburger-toggle .hamburger.active span:nth-child(2) {
    left: 50%;
    width: 0px;
}
.navbar-toggler .hamburger-toggle .hamburger.active span:last-child {
    top: 45%;
    transform: rotate(-45deg);
}
.navbar-wrap{
    background-color: var(--white);
    box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.33);
    margin-left: 180px;
    margin-right: 180px;
}
.navbar-wrap .navbar{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.navbar-wrap .categories-link {
    margin-right: 1rem;
    background: var(--primary);
    color: var(--white);
    padding: 0.8rem 1.5rem;
}
.navbar-wrap .nav-link{
    color: var(--black);
    font-size: 0.875rem;
    font-weight: 500;
    padding: 0.85rem 1rem;
    text-decoration: none;
}
.navbar-wrap .nav-item:not(:last-child){
    margin-right: 1.8rem;
}
.navbar-wrap .nav-link.active,
.navbar-wrap .nav-link:hover{
    color: var(--primary);
    text-decoration: none;
}
.header-category {
	position: relative;
    z-index: 4;
}
.cat-toggle{
    display: flex;
    align-items: center;
    justify-content: space-between;
    text-decoration: none;
}
.header-category > a {
    display: flex;
    align-items: center;
    padding: 16px 18px;
    min-width: 170px;
    font-size: 14px;
    text-transform: uppercase;
    color: var(--white);
    font-weight: 600;
    background: var(--primary);
}

.header-category .category-menu {
    position: absolute;
    left: 0;
    margin: 0;
    padding: 0;
    list-style-type: none;
    top: 100%;
    width: 100%;
    margin-top: 0;
    background: var(--light-gray);
    border-radius: 3px;
    z-index: 2;
}
.header-category .do-not-show {
    display: none;
}
.navbar-nav{
    list-style-type: none;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.header-category .category-menu > li > a{
    display: flex;
    color: #000000;
    font-size: 13px;
    background: transparent;
    position: relative;
    align-items: center;
    font-weight: 300;
    text-transform: uppercase;
    transition: .3s ease-in;
    padding: 10px 50px 10px 18px;
}
.header-category .category-menu > li > a:hover {
	color: var(--white);
	background: var(--footer-text);
}

.category-menu > li.menu-item-has-children > a::after {
    content: "\F285";
    position: absolute;
    right: 18px;
    font-size: 13px;
    font-family: bootstrap-icons!important;
    font-weight: 700;
    color: #000000;
    transition: .3s ease-in;
}

.category-menu > li.menu-item-has-children > a:hover::after {
	color: var(--white);
}

.header-category .category-menu > li > a > span {
    background: var(--primary);
    line-height: 1;
    border-radius: 10px;
    font-size: 10px;
    color: #fff;
    padding: 5px 10px;
    margin-left: 10px;
}

.header-category .category-menu > li > a i {
    font-size: 16px;
    margin-right: 10px;
    line-height: 0;
}
.header-category .category-menu > .add-megamenu > a {
    font-weight: 700;
    border-bottom: 1px solid #d3d2d2;
    padding: 18px 18px 14px;
    margin-bottom: 10px;
}
.header-category .category-menu > .add-megamenu > a i {
	color: var(--primary);
}

.category-menu .megamenu {
    position: absolute;
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    left: 100%;
    top: 0;
    background: #efefed;
    box-shadow: 0px 1px 16px 0px rgba(220, 220, 220, 0.47);
    border: 1px solid rgb(211 210 210 / 24%);
    width: 840px;
    min-height: 100%;
    padding: 20px 20px;
    text-align: left;
    opacity: 0;
    visibility: hidden;
    transform: translateY(10px);
    transition: .3s linear;
    z-index: 2;
}

.category-menu .megamenu ul{
    list-style-type: none;
    margin: 0;
    padding: 0;
}

.category-menu .megamenu > .sub-column-item {
    width: 33.3333%;
    flex: 0 0 33.3333%;
    margin-bottom: 30px;
	padding: 0 15px;
}

.category-menu .megamenu > .sub-column-item > a {
    font-weight: 500;
    display: block;
    color: #000000;
    font-size: 1rem;
    margin-bottom: 15px;
    line-height: 1.3;
}

.category-menu .megamenu > .sub-column-item > a:hover {
	color: var(--primary);
}

.category-menu .megamenu > .sub-column-item ul li {
    margin-bottom: 0.4rem;
}

.category-menu .megamenu > .sub-column-item ul li:last-child {
	margin-bottom: 0;
}

.category-menu .megamenu > .sub-column-item ul li a {
    display: inline-flex;
    color: var(--black);
    font-size: 13px;
    text-transform: uppercase;
}

.category-menu .megamenu > .sub-column-item ul li a:hover {
	color: var(--primary);
	padding-left: 5px;
}
.category-menu .megamenu > .sub-column-item .mega-menu-banner a:hover {
	padding: 0;
}
.mega-menu-banner img {
	max-width: 100%;
}
.category-menu > li:hover > .megamenu {
    opacity: 1;
    visibility: visible;
    transform: translateY(0);
}
.category-menu .more-categories {
    padding: 13px 18px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 5px;
    cursor: pointer;
    background: var(--primary);
    font-size: 13px;
    color: var(--white);
    text-transform: uppercase;
    font-weight: 600;
}
.category-menu .more-categories i {
	font-size: 12px;
	transform: rotate(0);
	transition: .3s linear;
}
.category-menu .more-categories.show i {
	transform: rotate(-90deg);
}

.header-support{
    border: none;
    margin: 0;
    margin-left: auto;
    margin-right: 5px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.header-support .call-us{
    color: rgb(187, 18, 74);
}
@media (max-width: 1200px) {
    .more_slide_open .megamenu, .category-menu .megamenu{
        width: 650px;
    }
}
@media (min-width: 992px){
    .navbar-toggler {
        display: none;
    }
}
@media (max-width: 991.99px) {
    .header-wrap .category-form {
        display: none;
    }
    .navbar-collapse{
        list-style: none;
        border-top: 1px solid var(--border-color);
    }
    .navbar-wrap .nav-link{
        padding: 0.5rem 1rem;
        text-decoration: none;
    }
    .header-support{
        border: none;
        margin: 0;
        margin-left: auto;
    }
}
